.slide {
  transform: translateX(0);
  transition: transform 2s ease;

  &.next {
    transform: translateX(100%);
    transition: transform 2s ease;
  }

  &.back {
    transform: translateX(-100%);
    transition: transform 2s ease;
  }

  &.active {
    transition: transform 2s ease;
  }
}
